#hipaaRibbon {
  position: static;
  width: 100%;
  padding-top: 10px;
  height: 60px;
}
#hipaaRibbon input[type="date"] {
  padding-left: 10px;
}
#hipaaRibbon select {
  display: inline-block;
  height: 40px;
}
#hipaaRibbon #hipaaSearchFilter {
  color: black;
}
#hipaaRibbon .form-control {
  display: inline-block !important;
}
#hipaaRibbon #searchClear {
  position: absolute;
  right: 14px;
  top: 0;
  bottom: 0;
  height: 14px;
  margin: auto;
  font-size: 14px;
  cursor: pointer;
  color: #ccc;
}
